___CSS_LOADER_EXPORT___.push([module.id, `#menu-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 30px;
}

#menu-container .card {
    padding: 30px;
    border-radius: 40px;
    border: 1px solid rgba(255, 255, 255, .25);
    background-color: rgba(255, 255, 255, 0.45);
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(15px);
}

#menu-container .card > img {
    border-radius: 8px;
}

#menu-container .card > h3 {
    font-size: 1.2rem;
    font-weight: 700;
}

#menu-container .card > p {
    font-size: 0.95rem;
    font-style: italic;
}

#menu-container .card > *:not(:first-child) {
    margin-top: 10px;
}

@media only screen and (max-width: 768px) {
    :root {
        font-size: 0.85rem;
    }

    #home-container {
        flex-direction: column;
    }
}`, "",{"version":3,"sources":["webpack://./src/components/menu/menu.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,0CAA0C;IAC1C,2CAA2C;IAC3C,4CAA4C;IAC5C,2BAA2B;AAC/B;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI;QACI,kBAAkB;IACtB;;IAEA;QACI,sBAAsB;IAC1B;AACJ","sourcesContent":["#menu-container {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));\n    gap: 30px;\n}\n\n#menu-container .card {\n    padding: 30px;\n    border-radius: 40px;\n    border: 1px solid rgba(255, 255, 255, .25);\n    background-color: rgba(255, 255, 255, 0.45);\n    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);\n    backdrop-filter: blur(15px);\n}\n\n#menu-container .card > img {\n    border-radius: 8px;\n}\n\n#menu-container .card > h3 {\n    font-size: 1.2rem;\n    font-weight: 700;\n}\n\n#menu-container .card > p {\n    font-size: 0.95rem;\n    font-style: italic;\n}\n\n#menu-container .card > *:not(:first-child) {\n    margin-top: 10px;\n}\n\n@media only screen and (max-width: 768px) {\n    :root {\n        font-size: 0.85rem;\n    }\n\n    #home-container {\n        flex-direction: column;\n    }\n}"],"sourceRoot":""}]);
